import AddTask from "./components/AddTask";
import Header from "./components/Header";
import Task from "./components/Tasks";
import { useState, useEffect } from "react"
import React from "react";

// PUT : update
// POST : add data to server
// DELETE : delete the record

// PUT and POST need to add headers and body, DELETE dosent need anything  

function App() {
  const [showAddTask, setShowAddTask] = useState(false)

  const [tasks, setTasks] = useState([])

  // second get the data to the browser
  useEffect(() => {
    const getTasks = async () => {
      const tasksFromServer = await fetchTasks()
      setTasks(tasksFromServer)
    }
    getTasks()
  },[])

  // first fetch data from server 
  const fetchTasks = async () => {
    const res = await fetch('http://localhost:5000/tasks')
    const data  = await res.json()

    return data
  }

  // fetch single task
  const fetchTask = async (id) => {
    const res = await fetch(`http://localhost:5000/tasks/${id}`)
    const data  = await res.json()

    return data
  }

  // Delete Tasks
  // simply hiding the tasks
  // const deleteTask = (id) => {
  //   setTasks(tasks.filter(task => task.id !== id))
  // }
  // deleting from the server + ui
  const deleteTask = async (id) => {
    await fetch(`http://localhost:5000/tasks/${id}`,{
      method:'DELETE'
    })

    setTasks(tasks.filter(task => task.id !== id))
  }

  // toggle reminder
  const toggleReminder = async (id) => {
    const taskToToggle = await fetchTask(id)
    const updTask = {...taskToToggle, reminder: !taskToToggle.reminder}

    const res = await fetch(`http://localhost:5000/tasks/${id}`,{
      method:'PUT',
      headers: {
        'Content-type': 'application/json'
      },
      body: JSON.stringify(updTask)
    })

    const data = await res.json();

    setTasks(tasks.map((task) => task.id === id
      ? { ...task, reminder: !data.reminder }
      : task))
  }

  // Add Task
  const addTask = async (task) => {
    const res = await fetch('http://localhost:5000/tasks', {
      method:'POST',
      headers: {
        'Content-type' : 'application/json'
      },
      body: JSON.stringify(task)
    })

    const data =await res.json()
    setTasks([...tasks, data])
    // const id = Math.floor(Math.random() * 10000) + 1;
    // const newTask = {id, ...task};
    // setTasks([...tasks, newTask])
  }

  return (
    <div className="container">
      <Header onAdd ={() => setShowAddTask(!showAddTask)} showAdd={showAddTask}/>
      {showAddTask && <AddTask onAdd={addTask} />}
      {tasks.length > 0 ? <Task tasks={tasks} onDelete={deleteTask} onToggle={toggleReminder} /> : "No Task to show!!"}
    </div>
  );
}

export default App;

