import React from "react"
import Button from "./Button"


const Header = ({ title, onAdd, showAdd }) => { // props is basically an object as shown in default props so we can destructure it as shown here and we dont need to write the props.title
    // const Header = (props) => {

    return (
        <header className="header">
            <h1>{title}</h1>
            <Button color={showAdd ? "red" : "green"} text={showAdd ? "Close" : "Add"} onClick={onAdd} />
        </header>
    )
}

Header.defaultProps = {
    title: "Task Tracker"
}

export default Header