import { useState } from "react"
import React from "react";

const AddTask = ({onAdd}) => {
    const [text, setText] = useState('');
    const [day, setDay] = useState('');
    const [reminder, setReminder] = useState(false);

    const onSubmit = (e) => {
        e.preventDefault();

        if(!text) {
            alert("Plz add Task")
            return
        }

        onAdd({text, day, reminder})

        setText('')
        setDay('')
        setReminder(false)
    }

    return (
        <form className='add-form' onSubmit={onSubmit}>
            <div className="form-control">
                <label htmlFor="">Task</label>
                <input type="text" name="" placeholder="Add Task" value={text} onChange={(e)=>{setText(e.target.value)}}/>
            </div>
            <div className="form-control">
                <label htmlFor="">Day and time</label>
                <input type="text" name="" placeholder="Add Day and Time" value={day} onChange={(e)=>{setDay(e.target.value)}}/>
            </div>
            <div className="form-control form-control-check">
                <label htmlFor="">Reminder</label>
                <input 
                    type="checkbox" 
                    value={reminder} 
                    onChange={(e)=>{setReminder(e.currentTarget.checked)}}
                    checked={reminder}/>
            </div>

            <input type="submit" value="Save Task" className="btn btn-block" />
        </form>
    )
}

export default AddTask